.checkbox {
  cursor: pointer;
  display: block;
  user-select: none;
}
.checkbox__label {
  background: transparent;
  border-radius: 3px;
  border: 1px solid #eee;
  cursor: pointer;
  height: 1.8rem;
  width: 1.8rem;
  display: inline-block;
  margin-right: 1rem;
  position: relative;
  vertical-align: middle;
  z-index: 1;
}
.checkbox__label:after {
  color: #111;
  content: "\f00c";
  display: none;
  font-family: 'FontAwesome';
  font-size: 1.3rem;
  left: 2px;
  position: absolute;
  top: 1px;
  z-index: 10;
}
.checkbox input {
  display: none;
}
.checkbox input:checked + .checkbox__label:after {
  display: block;
}
.checkbox label {
  color: #111;
  display: inline-block;
  cursor: pointer;
  vertical-align: middle;
}
.checkbox a {
  border-bottom: 1px dotted #bbb;
}
.radio {
  cursor: pointer;
  display: block;
  margin-bottom: 1rem;
  user-select: none;
}
.radio__label {
  background: transparent;
  border-radius: 50%;
  border: 1px solid #d6d6d6;
  cursor: pointer;
  height: 2.4rem;
  width: 2.4rem;
  display: inline-block;
  margin-right: 1rem;
  position: relative;
  vertical-align: middle;
  z-index: 1;
}
.radio__label:after {
  background-color: #1a82cb;
  border-radius: 50%;
  height: 1.4rem;
  width: 1.4rem;
  color: #fff;
  content: '';
  display: none;
  font-size: 1.5rem;
  left: 4px;
  position: absolute;
  top: 4px;
  z-index: 10;
}
.radio input {
  display: none;
}
.radio input:checked + .radio__label {
  background-color: #fff;
  border-color: #1a82cb;
  box-shadow: 0 0 0 1px #1a82cb;
}
.radio input:checked + .radio__label:after {
  display: block;
}
.radio label {
  color: #111;
  display: inline-block;
